.country-adding {
  position: relative;

  &--choose,
  &--new {
    width: 220px;
  }
}

.country-adding--choose span {
  &::before {
    background-color: @white-four;
    border-color: @cloudy-blue;
  }
}

.country-adding__chosen-country {
  display: inline-block;
  vertical-align: middle;
  flex-grow: 1;
  padding: 14px 10px 10px 10px;
  white-space: nowrap;
  overflow: hidden;

  text-transform: uppercase;

  border-radius: 4px;
  border: 1px solid @cloudy-blue;
  background-color: @white-four;

  &:hover,
  &:focus {
    border-color: @dark-slate-blue;
  }

  &::after {
    content: "";
    position: absolute;
    top: 2px;
    left: 0;

    width: 100%;
    height: 90%;

    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 90%, rgba(255, 255, 255, 1) 100%);
  }
}

.country-adding__choose-country {
  display: inline-block;
  vertical-align: middle;
  padding: 14px 10px 10px 10px;
  width: 100%;

  text-transform: uppercase;

  border-radius: 4px;
  border: 1px solid @cloudy-blue;
  background-color: @white-four;

  &:hover,
  &:focus {
    border-color: @dark-slate-blue;
  }
}

.country-adding__choose-country--expand {
  color: @white-four;

  background-color: @dark-slate-blue;
  border: none;

  &::before {
    display: none;
  }
}

