.level {
  // position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  padding: 5px 0 0 2px;

  border-radius: 50%;

  &--middle {
    width: 60px;
    height: 60px;
  }

  &--big {
    width: 100px;
    height: 100px;
  }

  &--dark {
    background-color: rgba(0, 0, 0, 0.5);
  }
}

